.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: radial-gradient(circle at top, #00ffff33, #000011);
  color: white;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  padding: 20px;
}

.title {
  font-size: 3rem;
  font-weight: bold;
  background: linear-gradient(90deg, #00ffff, #0077ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 10px;
  text-align: center;
}

.subtitle {
  opacity: 0.9;
  margin-bottom: 30px;
  font-size: 1.1rem;
  text-align: center;
}

.card {
  background: rgba(255, 255, 255, 0.08);
  border-radius: 20px;
  padding: 30px 40px;
  backdrop-filter: blur(10px);
  text-align: center;
  min-width: 300px;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.walletInfo {
  font-size: 0.9rem;
  opacity: 0.8;
  margin-bottom: 5px;
}

.address {
  font-family: 'Courier New', monospace;
  font-size: 0.85rem;
  word-break: break-all;
  margin-bottom: 20px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}

.button {
  margin-top: 15px;
  background: linear-gradient(90deg, #00ffff, #0055ff);
  border: none;
  padding: 14px 28px;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  width: 100%;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 255, 255, 0.3);
}

.button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 255, 255, 0.5);
  opacity: 0.95;
}

.button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.buttonSecondary {
  margin-top: 10px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 10px 20px;
  border-radius: 8px;
  color: white;
  font-size: 0.9rem;
  cursor: pointer;
  width: 100%;
  transition: all 0.3s ease;
}

.buttonSecondary:hover {
  background: rgba(255, 255, 255, 0.2);
}

.status {
  margin-top: 20px;
  font-size: 1rem;
  opacity: 0.9;
  min-height: 24px;
  word-break: break-word;
}

.info {
  margin-top: 30px;
  text-align: center;
  opacity: 0.8;
  font-size: 0.9rem;
}

.link {
  display: inline-block;
  margin-top: 10px;
  color: #00ffff;
  text-decoration: none;
  transition: opacity 0.3s ease;
}

.link:hover {
  opacity: 0.7;
  text-decoration: underline;
}
